import axios, { AxiosResponse } from 'axios';

const API_DOMAIN = "https://604b401bee7cb900176a1a9e.mockapi.io/";
const PREFIX_DOMAIN = "api/v1/";
const LINE_DOMAIN = 'https://api.line.me/';

export class APIService {
  constructor() {
    axios.defaults.baseURL = `${API_DOMAIN}${PREFIX_DOMAIN}`;
    // this.setCommonHeader()
    this.setInterceptors();
  }

  setCommonHeader() {
    axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*';
    axios.defaults.headers.common['Access-Control-Allow-Methods'] = 'GET,POST,OPTIONS';
    axios.defaults.headers.common['Access-Control-Allow-Headers'] = 'Content-Type';
  }

  setInterceptors() {
    axios.interceptors.request.use((request: any) => {
      if (!request.url.includes(LINE_DOMAIN)) {
        const token = localStorage.getItem('ACCESS-TOKEN');
        if (token) {
          request.headers['Authorization'] = `Bearer ${token}`;
        }
      }
      return request;
    }, (error) => {
      return Promise.reject(error);
    });
    axios.interceptors.response.use(
      (response) => response,
      (error) => {
        // handle error
        if ( error.response && error.response.status === 401 ) {
          localStorage.removeItem('ACCESS-TOKEN');
          localStorage.removeItem('LINE-TOKEN');
          // location.reload();
          return Promise.reject( error );
        }
      });
  }

  public createURL(uri: (string | object)[]) {
    let paramsUrl: any;
    if (typeof uri[uri.length - 1] !== 'string') {
      paramsUrl = uri.pop();
      let url = uri.join('/');
      Object.keys(paramsUrl).forEach(x => {
        url = url.replace(`:${x}`, paramsUrl[x]);
      });
      return url;
    } else {
      return uri.join('/');
    }
  }

  public get(uri: (string | object)[], options = {}) {
    return new Promise((resolve, reject) => {
      axios.get(this.createURL(uri), {
        params: options,
      })
      .then((resp: AxiosResponse) => {
        resolve(resp.data);
      })
      .catch((err: any) => {
        reject(err);
      });
    });
  }

  public post(uri: (string|object)[], data = {}, moreConfigs = {}) {
    return new Promise((resolve, reject) => {
      axios.post(this.createURL(uri), data, moreConfigs)
        .then((resp: AxiosResponse) => {
          resolve(resp.data);
        })
        .catch((err: any) => {
          reject(err);
        });
    });
  }

  public put(uri: (string|object)[], data = {}) {
    return new Promise((resolve, reject) => {
      axios.put(this.createURL(uri), data)
        .then((resp: AxiosResponse) => {
          resolve(resp.data);
        })
        .catch((err: any) => {
          reject(err);
        });
    });
  }

  public delete(uri: (string|object)[]) {
    return new Promise((resolve, reject) => {
      axios.delete(this.createURL(uri))
        .then((resp: AxiosResponse) => {
          resolve(resp.data);
        })
        .catch((err: any) => {
          reject(err);
        });
    });
  }

  _form(data: any) {
    const formData = new FormData();
    for (const param in data) {
      if (data.hasOwnProperty(param)) {
        if (Array.isArray(data[param]) && data[param].length > 0) {
          data[param].forEach((item: any, index: number) => {
            formData.append(`${param}[${index}]`, item);
          });
        } else {
          formData.append(param, data[param]);
        }
      }
    }
    return formData;
  }

  public multipeRequests(apiReqs: any = []) {
    return new Promise( ( resolve, reject ) => {
      axios.all(apiReqs)
        .then((resp: any) => {
          resolve(resp.map((v: any) => v.data ));
        })
        .catch((err: any) => reject(err));
    });
  }
}
