import { useEffect, useState } from 'react';

import Navigation from './Navigation';
import Cart from './Cart';
import Search from './Search';

import Logo from '../assets/ecolife.jpg';

type Props = {
  children: React.ReactNode
};

const listMenu = [
  { id: 1, title: "Home", link: "/", submenu: null },
  { id: 2, title: "Fresh Food", link: "/fresh-food", submenu: null },
  {
    id: 3,
    title: "Fresh Vegetables",
    link: "/fresh-vegetables",
    submenu: [
      { id: 20, title: "Potatoes & Sweet Potatoes", link: "/potatoes-sweet-patatoes", submenu: null },
      { id: 21, title: "Onions & Leeks", link: "/onions-leeks", submenu: null },
      { id: 22, title: "Carrots & Root Vegetables", link: "/carrots-root-vegetables", submenu: null },
      { id: 23, title: "Broccoli & Cauliflower", link: "/broccoli-cauliflower", submenu: null },
      { id: 24, title: "Cabbages & Greens", link: "/cabbages-greens", submenu: null }
    ]
  },
  { id: 4, title: "Prices drop", link: "/prices-drop", submenu: null },
  { id: 5, title: "Contact us", link: "/contact-us", submenu: null },
]


const Header = ({ children }: Props) => {
  const [scrollTop, setScrollTop] = useState(0);
  const [scrolling, setScrolling] = useState(false);

  useEffect(() => {
    const onScroll = (e: any) => {
      setScrollTop(e.target.documentElement.scrollTop);
      if (e.target.documentElement.scrollTop > 100) {
        setScrolling(true);
      } else {
        setScrolling(false);
      }
    };
    window.addEventListener("scroll", onScroll);

    return () => window.removeEventListener("scroll", onScroll);
  }, [scrollTop]);

  return (
    <header className="header">
      <div className={"header-top d-flex align-center " + (scrolling ? 'header-scroll animated fadeInDown' : '')}>
        <div className="container">
          <div className="row align-center">
            <div id="logo" className="col col-2">
              <h1><img src={Logo} alt=""/></h1>
            </div>
            <div className="col col-10">
              <div className="d-flex justify-content-between align-center">
                <Navigation listMenu={listMenu} />
                <div className="header-search-cart">
                    <Search />
                    <Cart />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </header>
  );
}

export default Header;
