import ArrowDown from '../assets/svg/arrow-down.svg';

export interface Menu {
  id: number,
  title: string,
  submenu: Array<Menu> | null,
  link: string
}

type Props = {
  menuItem: Menu
}

const NavItem = ({ menuItem }: Props) => {
  return (
    <li className={menuItem.submenu ? "menu-item has-child" : "menu-item"} key={menuItem.id}>
      <a className="menu-link" href={menuItem.link}>
        <span>{menuItem.title}</span>
        {menuItem.submenu && <img className="dropdown-menu" src={ArrowDown} alt="" />}
      </a>

      {
        menuItem.submenu && (
          <div className="menu-dropdown">
            <ul className="sub-menu">
              {menuItem.submenu.map((subitem) => {
                return (
                <li className="sub-menu-item" key={subitem.id}>
                  <a href={subitem.link}>
                    <span>{subitem.title}</span>
                  </a>
                </li>)
              })}
            </ul>
          </div>
        )
      }
    </li>
  );
}

export default NavItem;
