import { PROFILE } from '../actions/profile';

let initialState = {
  status: false,
};
const profileReducer = (state = initialState, action: any) => {
  switch (action.type) {
    case PROFILE.UPDATESTATUS:
      const status = action.payload;
      return {
        ...state,
        status
      };
    default:
      return state;
  }
};

export default profileReducer;
  