import React from 'react';
import StarRatingComponent from 'react-star-rating-component';

import TitleSection from './TitleSection';

export interface Product {
  id: string,
  cover: string,
  name: string,
  price: string,
  sales: string,
  stars: string,
  type: string
}
type Props = {
  products: Array<Product>
}

const showRating = (stars: string) => {
  return (
    <StarRatingComponent 
      name="rate3"
      starCount={5}
      value={+stars}
      emptyStarColor="#d0d0d0"
      editing={false}
    />
  );
}

const FeaturedProducts = ({ products }: Props) => {
  console.log("products", products);

  const RenderSalesToPrice = (sales: string, price: string) => {
    let splitSales = sales.split('');

    if(splitSales && splitSales.length > 0 && splitSales.includes("-")) {
      if (splitSales.includes("%")) {
        const newPrice =  +price - (+price / parseFloat(sales.replace(/[-,%]/g, "")));
        return (<div className="product-price-and-shipping">
            <span className="regular-price">${price}</span>
            <span className="new-price">${newPrice}</span>
          </div>
        );
      } else {
        const newPrice =  +price - parseFloat(sales.replace(/[-]/g, ""));
        return (<div className="product-price-and-shipping">
            <span className="regular-price">${price}</span>
            <span className="new-price">${newPrice}</span>
          </div>
        )
      }
    } else {
      return ( 
        <div className="product-price-and-shipping">
          <span className="price">${price}</span>
        </div>
      );
    }
  }

  return (
    <React.Fragment>
      <TitleSection title="Featured Products" subtitle="Add featured products to weekly line up" />
      <div className="container mt-20">
      <div className="row">
        {products.map((product, index) => {
          return (
            <article className="col col-3" data-id-product={product.id} key={index}>
              <div className="product product-feature">
                <div className="picture">
                  <a href={product.cover}>
                    <img src={product.cover} alt="" />
                  </a>
                  <div className="quick-view"></div>
                </div>
                <div className="product-content">
                  <div className="cate">DUMMY CATEGORY</div>
                  <h3 className="name">{product.name}</h3>
                  <div className="hook-reviews">
                    {showRating(product.stars)}
                  </div>
                  { RenderSalesToPrice(product.sales, product.price) }
                </div>
              </div>
            </article>
          );
        })}
      </div>
      </div>
    </React.Fragment>
  );
}

export default FeaturedProducts;
