import NavItem, { Menu } from './NavItem';

type Props = {
  listMenu: Array<Menu>
};

const Navigation = ({ listMenu }: Props) => {
  return (
    <nav className="header-menu menu">
      <ul className="d-flex">
        {listMenu.map((item) => <NavItem menuItem={item} key={item.id} />)}
      </ul>
    </nav>
  );
}

export default Navigation;
